.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: rgba(255, 255, 255, 0.8);
  padding: 0.5rem 1rem;
  border-radius: 10px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.08);
  margin-bottom: 1rem;
}
/* グラフを左右に並べるためのコンテナ */
.charts-row {
  display: flex;
  flex-wrap: wrap;
  gap: 24px;
}

/* charts-row 内の chart-box は幅を均等に広げる */
.charts-row .chart-box {
  flex: 1;
}
.left-section {
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

.app-title {
  font-size: 18px;
  font-weight: bold;
  color: #2c3e50;
  margin: 0;
}

.hamburger {
  font-size: 18px;
  cursor: pointer;
  user-select: none;
  padding: 4px 8px;
  background-color: transparent;
  border: none;
  color: #333;
}

.user-info {
  font-size: 14px;
  color: #555;
  font-weight: bold;
  background-color: #f2f2f2;
  padding: 4px 10px;
  border-radius: 6px;
}

.menu {
  position: absolute;
  top: 60px;
  left: 10px;
  background-color: white;
  border: 1px solid #ccc;
  padding: 10px;
  z-index: 10;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: column;
  gap: 8px;
  width: 160px;
}

.menu-button {
  background-color: transparent;
  color: #333;
  border: none;
  padding: 10px;
  border-radius: 4px;
  cursor: pointer;
  text-align: left;
  width: 100%;
  font-size: 14px;
  font-weight: bold;
  transition: background-color 0.2s;
}

.menu-button:hover {
  background-color: #f0f0f0;
}

.menu-button.logout {
  background-color: #e74c3c;
  color: white;
}

.menu-button.logout:hover {
  background-color: #c0392b;
}

/* 棒グラフ用 */
.chart-box {
  margin: 24px 0;
  padding: 16px;
  border: 1px solid #ccc;
  background: #fefefe;
  border-radius: 8px;
}

.bar-row {
  display: flex;
  align-items: center;
  margin-bottom: 8px;
  cursor: pointer;
}

.bar-label {
  width: 120px;
  font-weight: bold;
}

.bar-container {
  flex-grow: 1;
  background-color: #eee;
  border-radius: 4px;
  overflow: hidden;
  margin-left: 8px;
}

.bar {
  background-color: #4caf50;
  height: 24px;
  color: white;
  padding-left: 8px;
  display: flex;
  align-items: center;
  font-size: 14px;
  border-radius: 4px;
}

/* 稼働年数別の棒グラフには青系の色を使用 */
.bar-year {
  background-color: #2196F3;
}

/* 詳細リスト */
.detail-list {
  margin-top: 24px;
}
